import { Component, OnInit } from '@angular/core';
import { TeacherService } from '../shared/teacher.service';
import { Teacher } from '../shared/teacher.model';
import { Observable } from 'rxjs/Observable';
import { AngularFireStorage } from 'angularfire2/storage';
import { FirebaseApp } from 'angularfire2';
@Component({
  selector: 'app-teacher-list',
  templateUrl: './teacher-list.component.html',
  styleUrls: ['./teacher-list.component.css']
})
export class TeacherListComponent implements OnInit {
  facultyPath:any;
  teacherList:Teacher[];
  store:any;
  
  
  

  constructor(private teacherService:TeacherService,private storage: AngularFireStorage) {
    


   }
   


  ngOnInit() {

    var x= this.teacherService.getData();
   x.snapshotChanges().subscribe(item=>{
     this.teacherList=[];
     item.forEach(element=>{
       var y=element.payload.toJSON();
       y["$key"]=element.key;
       this.teacherList.push(y as Teacher);
     });
   });
  }
onItemClick(teacher:Teacher) {
  this.teacherService.selectedTeacher=Object.assign({},teacher);
  // code is fine but better option is used for performance: this.teacherService.selectedTeacher=teacher;
  

}


}



