import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Post } from '../post.model';
import { PostService } from '../../../shared/post.service';
import { Router } from '@angular/router';
import { CanComponentDeactivate } from './can-deactivate-guard.service';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-new-post',
  templateUrl: './new-post.component.html',
  styleUrls: ['./new-post.component.css']
})
export class NewPostComponent implements CanComponentDeactivate,OnInit {
  
  @ViewChild('f') PostForm:NgForm;
  post:Post;
  confirmed:boolean=false;
  confirmButtonText="confirm data";
  postAdded=false;
  constructor(private postService:PostService,private router:Router) { }
  
  ngOnInit() {
  }
  canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
    if(this.PostForm.touched||this.PostForm.dirty){
      return confirm('you are in the mid of entering post data.do you wish to leave?');
    }
    
    else{
      return true;
    }
  }
  onConfirm(){ if(this.PostForm.valid){
    this.confirmed=true;
    this.confirmButtonText="data confirmed";
  }
  else {
    return alert('Post is incomplete or invalid.');
  }

  }

  onAddPost(){
  if(this.confirmed)
   { console.log("form has been submitted");
    console.log(this.PostForm);
  this.post=new Post(this.PostForm.value.imageUrl,this.PostForm.value.title,this.PostForm.value.text,this.PostForm.value.footerText);
  
  
  this.postService.addPost(this.post).subscribe((response)=>console.log(response),(error)=>console.log(error));
  
  
  console.log("the data entered in the newly created post by you is as follows ");
  console.log(this.post);
  console.log('post has been added succcessfully');
  this.postAdded=true;
  this.PostForm.reset();
  }
   else{
return alert(' please confirm the data first');
   }

  }
  
}



