import { Component, OnInit } from '@angular/core';
import { CprojectService } from '../shared/cproject.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-cproject',
  templateUrl: './cproject.component.html',
  styleUrls: ['./cproject.component.css']
})
export class CprojectComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
  constructor(private cprojectService:CprojectService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.cprojectService.insertCproject(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.cprojectService.updateCproject(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.cprojectService.selectedCproject={
  $key:null,
  title:'',
  principleInvestigator:'',
  coInvestigator:'',
  sponsoringAgency:''
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.cprojectService.deleteCproject(form.value.$key);
    this.resetForm(form);
  }

}
}



