import { Component, OnInit } from '@angular/core';
import { News } from '../home/shared/news.model';
import { Router } from '@angular/router';
import { NewsService } from '../home/shared/news.service';

@Component({
  selector: 'app-allnews',
  templateUrl: './allnews.component.html',
  styleUrls: ['./allnews.component.css']
})
export class AllnewsComponent implements OnInit {


  newsList:News[];

  constructor(private router:Router,private newsService:NewsService) {



   }

  ngOnInit() {


    var y= this.newsService.getDataFull();
    y.snapshotChanges().subscribe(item=>{
      this.newsList=[];
      item.forEach(element=>{
        var y=element.payload.toJSON();
        y["$key"]=element.key;
        this.newsList.push(y as News);
      });
    });
  }
  onClickEdit(){
    this.router.navigate(['/news-list-edit']);}


  onClickNewsItem(news:News){
    this.newsService.selectedNews=Object.assign({},news);
  
    this.router.navigate(['/news-details']);
    
  }
}



