import { Component, OnInit } from '@angular/core';
import { Oproject } from '../shared/oproject.model';
import { OprojectService } from '../shared/oproject.service';

@Component({
  selector: 'app-oproject-list',
  templateUrl: './oproject-list.component.html',
  styleUrls: ['./oproject-list.component.css']
})
export class OprojectListComponent implements OnInit {
  oprojectList:Oproject[];
  

  constructor(private oprojectService:OprojectService) { }

  ngOnInit() {
    var x= this.oprojectService.getData();
   x.snapshotChanges().subscribe(item=>{
     this.oprojectList=[];
     item.forEach(element=>{
       var y=element.payload.toJSON();
       y["$key"]=element.key;
       this.oprojectList.push(y as Oproject);
     });
   });
  }
onItemClick(oproject:Oproject) {
  this.oprojectService.selectedOproject=Object.assign({},oproject);
  // code is fine but better option is used for performance: this.teacherService.selectedTeacher=teacher;
  

}

}



