import { Component, OnInit } from '@angular/core';
import { LinksService } from '../shared/links.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-links',
  templateUrl: './links.component.html',
  styleUrls: ['./links.component.css']
})
export class LinksComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
  constructor(private linksService:LinksService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.linksService.insertLinks(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.linksService.updateLinks(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.linksService.selectedLinks={
  $key:null,
  name:'',
  url:'',
  
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.linksService.deleteLinks(form.value.$key);
    this.resetForm(form);
  }

}
}


