

import { Injectable } from '@angular/core';
import { Link } from './link.model';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database';
@Injectable()
export class LinksService {
  linksList:AngularFireList<any>;
  selectedLinks:Link=new Link();
  constructor(private firebase:AngularFireDatabase) { }

  getData() {
    this.linksList=this.firebase.list('home/links');
    return this.linksList; 
  }
  insertLinks(links:Link)
  {
    this.linksList.push({
      name:links.name,
      url:links.url,
      
    })

  }
  updateLinks(links:Link){
    this.linksList.update(links.$key,{name:links.name,
    url:links.url
    });
  }

  deleteLinks(key:string)
  {this.linksList.remove(key);}
}



