import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as firebase from 'firebase';
import { AuthService } from './auth.service';

interface DataResponse {
  userId: string;
  id: string;
  title: string;
}

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  email: string;
  password: string;

  shouldRun = [/(^|\.)plnkr\.co$/, /(^|\.)stackblitz\.io$/].some(h => h.test(window.location.host));
  title = 'Department Of Design IIT Guwahati';

  constructor( private http: HttpClient,public authService: AuthService ) { //dependency injection, creating an instance of HttpClient called http

  }

  ngOnInit(): void { // adding the lifecycle hook ngOnInit
    firebase.initializeApp({apiKey: "AIzaSyDY_V6wOWaJ3efjLKrcG2fHTW6OpFQq9uU",
    authDomain: "site-one-bc07f.firebaseapp.com",
    databaseURL: "https://site-one-bc07f.firebaseio.com",
    projectId: "site-one-bc07f",
    storageBucket: "site-one-bc07f.appspot.com",
    messagingSenderId: "778108263344"})
    this.http.get<DataResponse>('http://jsonplaceholder.typicode.com/posts/1').subscribe(data => {
      console.log('UserId: ' + data.userId);
      console.log('Id: ' + data.id);
      console.log('Title: ' + data.title);
    });
    this.http.post('http://jsonplaceholder.typicode.com/posts', {
      title: 'foo',
      body: 'bar',
      userId: 1
    })
      .subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log('Error occured');
        }
      );
      
  }
  signup() {
    this.authService.signup(this.email, this.password);
    this.email = this.password = '';
  }

  login() {
    this.authService.login(this.email, this.password);
    this.email = this.password = '';    
  }

  logout() {
    this.authService.logout();
  }


}



