import { Component, OnInit } from '@angular/core';
import { OprojectService } from '../shared/oproject.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-oproject',
  templateUrl: './oproject.component.html',
  styleUrls: ['./oproject.component.css']
})
export class OprojectComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
  constructor(private oprojectService:OprojectService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.oprojectService.insertOproject(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.oprojectService.updateOproject(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.oprojectService.selectedOproject={
  $key:null,
  title:'',
  principleInvestigator:'',
  coInvestigator:'',
  sponsoringAgency:''
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.oprojectService.deleteOproject(form.value.$key);
    this.resetForm(form);
  }

}
}



