

import { Injectable } from '@angular/core';
import { News } from './news.model';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database';
@Injectable()
export class NewsService {
  newsList:AngularFireList<any>;
  selectedNews:News=new News();
  constructor(private firebase:AngularFireDatabase) { }

  getData() {
    this.newsList=this.firebase.list('home/news',firebase=>firebase.limitToLast(4));
    return this.newsList; 
  }
  getDataFull(){
    this.newsList=this.firebase.list('home/news');
    return this.newsList;
  }
  insertNews(news:News)
  {
    this.newsList.push({
      title:news.title,
      content:news.content,
      image:news.image,
      date:news.date,
      
    })

  }
  updateNews(news:News){
    this.newsList.update(news.$key,{title:news.title,
    content:news.content,
    image:news.image,
    date:news.date,
    
    });
  }

  deleteNews(key:string)
  {this.newsList.remove(key);}
}



