import { Component, OnInit } from '@angular/core';
import { NewsService } from '../shared/news.service';
import { NgForm } from '@angular/forms';
import { AngularFireUploadTask, AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs/Observable';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.css']
})
export class NewsComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
uploadPercent: Observable<number>;
downloadURL:any;
uploadState:any;
 uploadedImage: any;
 temp:any; 
 task:AngularFireUploadTask;
  constructor(private newsService:NewsService,private storage: AngularFireStorage,public snackBar: MatSnackBar) {
    this.temp=this.storage;
   }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.newsService.insertNews(form.value);
  this.resetForm(); this.inserted=true;  this.openSnackBar("record inserted","")}
  else {
     this.newsService.updateNews(form.value);
    this.updated=true; this.openSnackBar("record updated",""); }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.newsService.selectedNews={
  $key:null,
  title:'',
  content:'',
  image:null,
  date:'',
  imageUpload:null
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.newsService.deleteNews(form.value.$key);
    this.resetForm(form);
  }

}
uploadFile(event) {
  const file = event.target.files[0];
  const filePath = 'facultyImages/'+this.newsService.selectedNews.title;

  this.task = this.storage.upload(filePath, file);
  this.openSnackBar("image started uploading!","");
  this.downloadURL = this.task.downloadURL();
  this.uploadedImage=this.downloadURL;  
  this.uploadPercent = this.task.percentageChanges();

  this.task.downloadURL().subscribe(url=>{this.newsService.selectedNews.image=url;});
  
  

  
  //this.teacherService.selectedTeacher.image
  
  
    
}
openSnackBar(message: string, action: string) {
  this.snackBar.open(message, action, {
    duration: 2000,
  });
}
}




