

import { Injectable } from '@angular/core';
import { Publications } from './publications.model';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database';
@Injectable()
export class PublicationsService {
  publicationsList:AngularFireList<any>;
  selectedpublications:Publications=new Publications();
  constructor(private firebase:AngularFireDatabase) { }

  getData() {
    this.publicationsList=this.firebase.list('home/publications');
    return this.publicationsList; 
  }
  insertPublications(publications:Publications)
  {
    this.publicationsList.push({ title:publications.title,
      content:publications.content,
      year:publications.year,
      
    })

  }
  updatePublications(publications:Publications){
    this.publicationsList.update(publications.$key,{title:publications.title,content:publications.content,
    year:publications.year
    });
  }

  deletePublications(key:string)
  {this.publicationsList.remove(key);}
}



