import { Component, OnInit } from '@angular/core';
import { Cproject } from '../shared/cproject.model';
import { CprojectService } from '../shared/cproject.service';

@Component({
  selector: 'app-cproject-list',
  templateUrl: './cproject-list.component.html',
  styleUrls: ['./cproject-list.component.css']
})
export class CprojectListComponent implements OnInit {
  cprojectList:Cproject[];
  

  constructor(private cprojectService:CprojectService) { }

  ngOnInit() {
    var x= this.cprojectService.getData();
   x.snapshotChanges().subscribe(item=>{
     this.cprojectList=[];
     item.forEach(element=>{
       var y=element.payload.toJSON();
       y["$key"]=element.key;
       this.cprojectList.push(y as Cproject);
     });
   });
  }
onItemClick(cproject:Cproject) {
  this.cprojectService.selectedCproject=Object.assign({},cproject);
  // code is fine but better option is used for performance: this.teacherService.selectedTeacher=teacher;
  

}

}



