import {NgModule} from '@angular/core';
import { Routes, RouterModule, CanDeactivate } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { ContactComponent } from './contact/contact.component';
import { WorkComponent } from './work/work.component';
import { PeopleComponent } from './people/people.component';
import { StudentsComponent } from './students/students.component';
import { FacultyComponent } from './faculty/faculty.component';
import { ResearchComponent } from './research/research.component';
import { NewPostComponent } from './home/posts/new-post/new-post.component';
import { CanDeactivateGuard } from './home/posts/new-post/can-deactivate-guard.service';
import { TeacherListEditComponent } from './faculty/teacher-list-edit/teacher-list-edit.component';
import { OprojectListEditComponent } from './research/oproject-list-edit/oproject-list-edit.component';
import { CprojectListEditComponent } from './research/cproject-list-edit/cproject-list-edit.component';
import { NewsListEditComponent } from './home/news-list-edit/news-list-edit.component';
import { LinksListEditComponent } from './home/links-list-edit/links-list-edit.component';
import { PublicationsListEditComponent } from './research/publications-list-edit/publications-list-edit.component';
import { StudentsListEditComponent } from './students/students-list-edit/students-list-edit.component';
import { AcademicsComponent } from './academics/academics.component';
import { PlacesComponent } from './places/places.component';
import { EventsComponent } from './events/events.component';
import { CoursesComponent } from './academics/courses/courses.component';
import { PlacementsComponent } from './academics/placements/placements.component';
import { AdmissionsComponent } from './academics/admissions/admissions.component';
import { CourseListEditComponent } from './academics/courses/course-list-edit/course-list-edit.component';
import { OverviewComponent } from './academics/overview/overview.component';
import { NewsDetailsComponent } from './home/news-details/news-details.component';
import { AllnewsComponent } from './allnews/allnews.component';


const appRoutes:Routes=[{path:'', component:HomeComponent },{path:'admissions', component:AdmissionsComponent },{path:'news-details', component:NewsDetailsComponent },
{path:'research', component:ResearchComponent },{path:'allnews', component:AllnewsComponent }
,{path:'course-list-edit', component:CourseListEditComponent }
,{path:'oproject-list-edit', component:OprojectListEditComponent }
,{path:'cproject-list-edit', component:CprojectListEditComponent },
{path:'home', component:HomeComponent },
{path:'students-list-edit', component:StudentsListEditComponent }
,{path:'news-list-edit', component:NewsListEditComponent }
,{path:'publications-list-edit', component:PublicationsListEditComponent }
,{path:'links-list-edit', component:LinksListEditComponent },
{path:'home/newPost', component:NewPostComponent ,canDeactivate:[CanDeactivateGuard]}
,
{path:'faculty', component:FacultyComponent }
,{path:'teacher-edit', component:TeacherListEditComponent },
{path:'students', component:StudentsComponent },
{path:'people', component:PeopleComponent }
,{path:'academics', component:AcademicsComponent , children:[{path:'courses', component:CoursesComponent },{path:'overview', component:OverviewComponent },
{path:'placements', component:PlacementsComponent },
{path:'admissions', component:AdmissionsComponent },] },
{path:'places', component:PlacesComponent },
{path:'events', component:EventsComponent },


{path:'contact', component:ContactComponent }]



@NgModule({

    imports:[RouterModule.forRoot(appRoutes)],
    exports:[RouterModule]
})
export class AppRoutingModule{}


