import { Component, OnInit } from '@angular/core';
import { PublicationsService } from '../shared/publications.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-publications',
  templateUrl: './publications.component.html',
  styleUrls: ['./publications.component.css']
})
export class PublicationsComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
  constructor(private publicationsService:PublicationsService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.publicationsService.insertPublications(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.publicationsService.updatePublications(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.publicationsService.selectedpublications={
  $key:null,
  title:'',
  content:'',
  year:null,
  
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.publicationsService.deletePublications(form.value.$key);
    this.resetForm(form);
  }

}
}


