import { Component, OnInit } from '@angular/core';
import { Oproject } from './shared/oproject.model';
import { OprojectService } from './shared/oproject.service';
import { Router } from '@angular/router';
import {AngularFireList} from 'angularfire2/database';
import { Cproject } from './shared/cproject.model';
import { CprojectService } from './shared/cproject.service';
import { Publications } from './shared/publications.model';
import { PublicationsService } from './shared/publications.service';
import { AuthService } from '../auth.service';
@Component({
  selector: 'app-research',
  templateUrl: './research.component.html',
  styleUrls: ['./research.component.css']
})
export class ResearchComponent implements OnInit {
  
  publicationsList:Publications[];
  
  cprojectList:Cproject[];
  
  oprojectList:Oproject[];
  constructor(private oprojectService:OprojectService,private publicationsService:PublicationsService,private auth: AuthService,private router:Router,private cprojectService:CprojectService) { }

  ngOnInit() {  var x= this.oprojectService.getData();
    x.snapshotChanges().subscribe(item=>{
      this.oprojectList=[];
      item.forEach(element=>{
        var y=element.payload.toJSON();
        y["$key"]=element.key;
        this.oprojectList.push(y as Oproject);
      });
    });
    var y= this.cprojectService.getData();
    y.snapshotChanges().subscribe(item=>{
      this.cprojectList=[];
      item.forEach(element=>{
        var y=element.payload.toJSON();
        y["$key"]=element.key;
        this.cprojectList.push(y as Cproject);
      });
    });
    var z= this.publicationsService.getData();
    z.snapshotChanges().subscribe(item=>{
      this.publicationsList=[];
      item.forEach(element=>{
        var z=element.payload.toJSON();
        z["$key"]=element.key;
        this.publicationsList.push(z as Publications);
      });
    });
  }
  onClickEdit(){
    this.router.navigate(['/oproject-list-edit']);
  }
  onClickEdit2(){
    this.router.navigate(['/cproject-list-edit']);
  }
  onClickEdit3(){
    this.router.navigate(['/publications-list-edit']);
    
  }
  
}



