import { Component, OnInit } from '@angular/core';
import { NewsService } from './shared/news.service';
import { Router } from '@angular/router';
import { News } from './shared/news.model';
import { Link } from './shared/link.model';
import { LinksService } from './shared/links.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  newsList:News[];
  linksList:Link[];
  constructor(private router:Router,private newsService:NewsService,private linksService:LinksService) { }

  ngOnInit() {  
    
    var y= this.newsService.getData();
    y.snapshotChanges().subscribe(item=>{
      this.newsList=[];
      item.forEach(element=>{
        var y=element.payload.toJSON();
        y["$key"]=element.key;
        this.newsList.push(y as News);
      });
    });

    var x= this.linksService.getData();
    x.snapshotChanges().subscribe(item=>{
      this.linksList=[];
      item.forEach(element=>{
        var x=element.payload.toJSON();
        x["$key"]=element.key;
        this.linksList.push(x as Link);
      });
    });
  }
  onClickEdit(){
    this.router.navigate(['/news-list-edit']);
  }
  onClickEdit2(){
    this.router.navigate(['/links-list-edit']);
  }
  onClickNewsItem(news:News){
    this.newsService.selectedNews=Object.assign({},news);
  
    this.router.navigate(['/news-details']);
    
  }
}



