import { Injectable } from '@angular/core';
import { Teacher } from './teacher.model';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database';
@Injectable()
export class TeacherService {
  teacherList:AngularFireList<any>;
  selectedTeacher:Teacher=new Teacher();
  constructor(private firebase:AngularFireDatabase) { }

  getData() {
    this.teacherList=this.firebase.list('teacher');
    return this.teacherList; 
  }
  insertTeacher(teacher:Teacher)
  {
    this.teacherList.push({
      name:teacher.name,
      position:teacher.position,
      department:teacher.department,
      image:teacher.image
    })

  }
  updateTeacher(teacher:Teacher){
    this.teacherList.update(teacher.$key,{name:teacher.name,
    position:teacher.position,
    department:teacher.department,
    image:teacher.image
    });
  }

  deleteTeacher(key:string)
  {this.teacherList.remove(key);}
}



