import { Post } from "../home/posts/post.model";
import { Http } from "@angular/http";
import { Injectable, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import {Response} from "@angular/http";
@Injectable()
export class PostService implements OnInit{
    posts:Post[]=[ new Post( 'https://5.imimg.com/data5/HQ/WW/MY-20499589/sitting-ganesh-ji-statue-500x500.jpg' , 'Jay Shri Ganesh ','Shri ganeshaye namah','Posted by Ayush sharma ' ) ]
    
    constructor(private http:HttpClient){}
    
    
getPosts(){
    return this.http.get('https://site-one-bc07f.firebaseio.com/.json');
}
    ngOnInit() {
        this.getPosts().subscribe( 
            (response:Response)=>{ const data=response.json(); this.posts=data;}
            ,
            (error)=>console.log(error)
                       );                  }
       
addPost(post:Post){
        
        this.posts.push(post); 
        return this.http.put('https://site-one-bc07f.firebaseio.com/.json',this.posts);

}
}


