import { Component, OnInit, Input } from '@angular/core';
import { StudentsService } from '../shared/students.service';
import {NgForm} from '@angular/forms';
import { AngularFireStorage, AngularFireUploadTask } from 'angularfire2/storage';
import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operator/map';
import { OnChanges } from '@angular/core/src/metadata/lifecycle_hooks';
import {MatSnackBar} from '@angular/material';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit,OnChanges {
inserted=false;
updated=false;
deleted=false;
uploadPercent: Observable<number>;
downloadURL:any;
uploadState:any;
 uploadedImage: any;
 temp:any; 
 task:AngularFireUploadTask;
 constructor(private studentsService:StudentsService,private storage: AngularFireStorage,public snackBar: MatSnackBar) { 
  this.temp=this.storage;
  }

  ngOnInit() {
      }
  ngOnChanges(){

  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.studentsService.insertStudents(form.value);
  this.resetForm(); this.inserted=true;  this.openSnackBar("record inserted","");}
  else {
     this.studentsService.updateStudents(form.value);
    this.updated=true; this.openSnackBar("record updated","");}

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.studentsService.selectedstudents={
  $key:null,
  name:'',
  year:null,
  course:'',
  interestArea:'',
  image:null,
  imageUpload:null
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.studentsService.deleteStudents(form.value.$key);
    this.resetForm(form);
    this.openSnackBar("record"," deleted!");
  }

}
uploadFile(event) {
  const file = event.target.files[0];
  const filePath = 'facultyImages/'+this.studentsService.selectedstudents.name;

  this.task = this.storage.upload(filePath, file);
  this.openSnackBar("image started uploading!","");
  this.downloadURL = this.task.downloadURL();
  this.uploadedImage=this.downloadURL;  
  this.uploadPercent = this.task.percentageChanges();

  this.task.downloadURL().subscribe(url=>{this.studentsService.selectedstudents.image=url;});
  
  

  
  //this.studentsService.selectedstudents.image
  
  
    
}
openSnackBar(message: string, action: string) {
  this.snackBar.open(message, action, {
    duration: 2000,
  });
}
}



