import { Component, OnInit } from '@angular/core';
import { StudentsService } from '../shared/students.service';
import { Students } from '../shared/students.model';
import { Observable } from 'rxjs/Observable';
import { AngularFireStorage } from 'angularfire2/storage';
import { FirebaseApp } from 'angularfire2';
@Component({
  selector: 'app-students-list',
  templateUrl: './students-list.component.html',
  styleUrls: ['./students-list.component.css']
})
export class StudentsListComponent implements OnInit {
  facultyPath:any;
  studentsList:Students[];
  store:any;
  
  
  

  constructor(private studentsService:StudentsService,private storage: AngularFireStorage) {
    


   }
   


  ngOnInit() {

    var x= this.studentsService.getData();
   x.snapshotChanges().subscribe(item=>{
     this.studentsList=[];
     item.forEach(element=>{
       var y=element.payload.toJSON();
       y["$key"]=element.key;
       this.studentsList.push(y as Students);
     });
   });
  }
onItemClick(students:Students) {
  this.studentsService.selectedstudents=Object.assign({},students);
  // code is fine but better option is used for performance: this.studentsService.selectedstudents=students;
  

}


}



