

import { Injectable } from '@angular/core';
import { Course } from './course.model';
import {AngularFireDatabase,AngularFireList} from 'angularfire2/database';
@Injectable()
export class CourseService {
  courseList:AngularFireList<any>;
  selectedcourse:Course=new Course();
  constructor(private firebase:AngularFireDatabase) { }

  getData() {
    this.courseList=this.firebase.list('academics/course');
    return this.courseList; 
  }
  insertcourse(course:Course)
  {
    this.courseList.push({
      name:course.name,
      heading:course.heading,
      
    })

  }
  updatecourse(course:Course){
    this.courseList.update(course.$key,{name:course.name,
    heading:course.heading
    });
  }

  deletecourse(key:string)
  {this.courseList.remove(key);}
}



