import { Component, OnInit } from '@angular/core';
import { Publications } from '../shared/publications.model';
import { PublicationsService } from '../shared/publications.service';

@Component({
  selector: 'app-publications-list',
  templateUrl: './publications-list.component.html',
  styleUrls: ['./publications-list.component.css']
})
export class PublicationsListComponent implements OnInit {
  publicationsList:Publications[];
  

  constructor(private publicationsService:PublicationsService) { }

  ngOnInit() {
    var x= this.publicationsService.getData();
   x.snapshotChanges().subscribe(item=>{
     this.publicationsList=[];
     item.forEach(element=>{
       var y=element.payload.toJSON();
       y["$key"]=element.key;
       this.publicationsList.push(y as Publications);
     });
   });
  }
onItemClick(publications:Publications) {
  this.publicationsService.selectedpublications=Object.assign({},publications);
  // code is fine but better option is used for performance: this.teacherService.selectedTeacher=teacher;
  

}

}




