import { Component, OnInit } from '@angular/core';
import { CourseService } from '../shared/course.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.css']
})
export class CourseComponent implements OnInit {

  inserted=false;
updated=false;
deleted=false;
  constructor(private courseService:CourseService) { }

  ngOnInit() {
  }
onSubmit(form:NgForm) { if(form.value.$key==null){
  this.courseService.insertcourse(form.value);
  this.resetForm(); this.inserted=true;}
  else {
     this.courseService.updatecourse(form.value);
    this.updated=true; }

}

resetForm(form?:NgForm) { 
  if(form!=null) 
  form.reset();
this.courseService.selectedcourse={
  $key:null,
  name:'',
  heading:'',
  
}
  
}

onDelete(form:NgForm){
  if(confirm("are you sure you want to delete this record")==true){
    this.courseService.deletecourse(form.value.$key);
    this.resetForm(form);
  }

}
}


